* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Merriweather, serif;
}

body {
  color: #333;
  background: #333;
  padding: 0;
  line-height: 1.6;
}

header {
  color: #fff;
  background-color: #000c;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  display: flex;
}

header .logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.nav-links {
  gap: 1.5rem;
  margin: 0;
  list-style: none;
  display: flex;
}

.nav-links li {
  position: relative;
}

.nav-links a {
  color: #fff;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: 1rem;
  text-decoration: none;
  transition: background-color .3s;
}

.nav-links a:hover {
  background-color: #fff3;
}

.featured-article {
  text-align: center;
  background-color: #ffffffe6;
  max-width: 800px;
  margin: 1rem auto;
  padding: 2rem;
  box-shadow: 0 0 10px #0003;
}

.featured-article img {
  width: 100%;
  height: auto;
}

.articles {
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
  margin: auto;
  padding: 1rem;
  display: flex;
}

.articles article {
  background-color: #ffffffe6;
  border-radius: 5px;
  width: calc(33% - 2rem);
  margin: 1rem;
  padding: 1rem;
  transition: transform .2s;
  box-shadow: 0 0 10px #0003;
}

.articles article:hover {
  transform: scale(1.05);
}

.articles article img {
  max-width: 100%;
  height: auto;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #000c;
  margin-top: 2rem;
  padding: 1rem 0;
}

.modal {
  z-index: 100;
  background-color: #000c;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background-color: #fff;
  width: 80%;
  margin: 15% auto;
  padding: 20px;
  position: relative;
}

.close-btn {
  color: #aaa;
  float: right;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
}

@media only screen and (width <= 768px) {
  .articles article {
    width: 100%;
  }

  header nav ul {
    flex-direction: column;
  }
}
